<template>
  <div>
      <div class="list" v-for="(item,index) in playlistDetail" :key="index">
          {{item.name}}
      </div>
  </div>

</template>

<script>
import request from "../../request/request.js";

export default {
    name:'Playlist',
    data(){
        return {
            playlistDetail:{}
        }
    },
    created(){
        this.getPlaylistDetail()
    },
    methods:{
        // 获取歌单详情
        getPlaylistDetail(){
            console.log('获取歌单详情')
            request({
                url:`/playlist/detail?id=${this.$route.params.id}`,
            }).then(Response=>{
                console.log(Response)
                this.playlistDetail = Response.data.playlist.tracks
                console.log(this.playlistDetail)
            }).catch(error=>{
                console.log(error)
            })
        }
    },
    watch: {
        '$route'(to, from) {
            // 对路由变化作出响应
            this.getPlaylistDetail()
        }
    }
}
</script>

